.react-tiny-popover-container {
  z-index: 1000;
}

.alert-enter {
  opacity: 0;
  margin: 30px;
  left: 60px;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.alert-enter-active {
  opacity: 1;
  left: 0px !important;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: opacity 300ms, left 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, left 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, transform 3000ms, left 300ms;
  transition: opacity 300ms, transform 3000ms, left 300ms, -webkit-transform 3000ms;
}
.alert-exit {
  left: 0px;
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.9) translateY(9);
          transform: scale(0.9) translateY(9);
  left: -60px;
  -webkit-transition: opacity 300ms, left 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, left 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, transform 3000ms, left 300ms;
  transition: opacity 300ms, transform 3000ms, left 300ms, -webkit-transform 3000ms;
}

/* ------------ */
/* Fade In Left     */
/* ------------ */
.fadeIn-enter {
  opacity: 0;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.fadeIn-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, transform 3000ms, right 300ms;
  transition: opacity 300ms, transform 3000ms, right 300ms, -webkit-transform 3000ms;
}
.fadeIn-exit {
  opacity: 1;
}
.fadeIn-exit-active {
  opacity: 0;
  -webkit-transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, transform 3000ms, right 300ms;
  transition: opacity 300ms, transform 3000ms, right 300ms, -webkit-transform 3000ms;
}

/* ------------ */
/* Fade In Left     */
/* ------------ */
.fadeInLeft-enter {
  opacity: 0;
  right: -100px !important;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.fadeInLeft-enter-active {
  opacity: 1;
  right: 0px !important;
  -webkit-transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, transform 3000ms, right 300ms;
  transition: opacity 300ms, transform 3000ms, right 300ms, -webkit-transform 3000ms;
}
.fadeInLeft-exit {
  opacity: 1;
  right: 0px !important;
}
.fadeInLeft-exit-active {
  opacity: 0;
  right: -100px !important;
  -webkit-transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, right 300ms, -webkit-transform 3000ms;
  transition: opacity 300ms, transform 3000ms, right 300ms;
  transition: opacity 300ms, transform 3000ms, right 300ms, -webkit-transform 3000ms;
}

.fade {
  opacity: 0;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  right: 0px;
}

.fade-enter-done {
  opacity: 1;
  -webkit-transform: translatey(0px);
          transform: translatey(0px);
  -webkit-transition: all 250ms ease-in;
  transition: all 250ms ease-in;
}

.fade-exit {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  z-index: 1;
}

.fade-enter.fade-exit-active {
  opacity: 0.4;
  -webkit-transform: scale(0.9) translateY(9);
          transform: scale(0.9) translateY(9);
  right: 0px;
  -webkit-transition: opacity 750ms ease-in;
  transition: opacity 750ms ease-in;
}

